.tag-filter {
  margin-bottom: 1rem;
}

.tag-filter-toggle {
  background: #333;
  border: none;
  padding: 0.5rem 1rem;
  font-family: "Heebo";
  font-size: 5rem;
  text-align: center;
  margin-top: 3vh;
  width: 100%;
  cursor: pointer;
  border-radius: 207px;
  color: #eee;


}

.tag-filter-toggle:hover {
  background: #555; /* כהה יותר בהאובר */
}
.tag-list {
  display: flex
  ;
      flex-wrap: wrap;
      gap: 20px 30px;
      padding: 0;
      max-width: 904px;
      margin-top: 3vh;
      border: none;
      background: transparent;
      list-style: none;
      justify-content: center;
}

.tag-item {
  width: 400px;
  background-color: #444;
  color: #ddd;
  font-family: "Heebo";
  padding: 6px 12px;
  border-radius: 63px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  cursor: pointer;
  user-select: none;
  font-size: 4.1rem;
  transition: background-color 0.3s ease, color 0.3s ease;
  text-align: center;
  box-sizing: border-box;

  white-space: nowrap;       /* מונע ירידת שורה */
  overflow: hidden;          /* מסתיר טקסט עודף */
  text-overflow: ellipsis;   /* מוסיף '…' אם הטקסט נחתך */
}



/* צבע רקע וכותרת בזמן מצב ה-hover */
.tag-item:hover {
  background-color: #1e90ff;
  color: white;
}

/* מחלקת active - הצבע כשנבחר */
.tag-item.active {
  background-color: #007bff; /* כחול יותר מודגש */
  color: white;
}

/* הסתרת צ'קבוקס אם קיים עדיין ב-HTML */
.tag-item input[type="checkbox"] {
  display: none;
}
.date-picker-container .react-datepicker {
        width: 1000px !important;
