}

.date-picker-container .react-datepicker__month {
  display: flex !important;
  flex-wrap: wrap !important;
  width: 100% !important;
  padding: 1rem 0 !important;
  justify-content: flex-start !important;
}

.date-picker-container .react-datepicker__week {
  display: flex !important;
  width: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  justify-content: space-between !important;
}

.date-picker-container .react-datepicker__day-names {
  display: flex !important;
  justify-content: space-between !important;
  padding: 0 10px !important;
  margin-bottom: 1rem !important;
  width: 100% !important;
  box-sizing: border-box !important;
  gap: 10px !important;
}

.date-picker-container .react-datepicker__day-name {
  flex: 1 !important;  /* תופס מקום שווה */
  max-width: none !important;
  border-radius: 0 !important; /* מסיר עיגול */
  width: auto !important;
  height: auto !important;
  line-height: normal !important;
  font-size: 2.5rem !important;
  font-weight: 600 !important;
  padding: 0.5rem 0 !important;
  color: white !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  margin: 0 !important;
  background: transparent !important;
  border: none !important;
}


.date-picker-container .react-datepicker__day {
  flex: 1 0 calc(100% / 7 - 10px) !important;
  max-width: calc(100% / 7 - 10px) !important;
  margin: 0.2rem !important;
  text-align: center !important;
  border-radius: 50% !important;
  width: auto !important;
  height: 150px !important;
  line-height: 60px !important;
  font-size: 3rem !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  box-sizing: border-box !important;
  color: #e0e0e0 !important;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.date-picker-container .react-datepicker__day:hover {
  background-color: #3700b3 !important;
  color: white !important;
}

.date-picker-container .react-datepicker__day--selected,
.date-picker-container .react-datepicker__day--keyboard-selected {
  background-color: #bb86fc !important;
  color: #121212 !important;
  border-radius: 50% !important;
  font-size: 3rem !important;
  width: 60px !important;
  height: 150px !important;
  line-height: 60px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  margin: 0.2rem !important;
}

.date-picker-container input.react-datepicker-ignore-onclickoutside {
  font-size: 3rem !important;
  padding: 20px 25px !important;
  background-color: #121212 !important;
  color: #e0e0e0 !important;
  border: 1px solid #444 !important;
  border-radius: 12px !important;
  width: 100% !important;
  box-sizing: border-box !important;
}

.date-picker-container input::placeholder {
  color: #888 !important;
  font-size: 3rem !important;
}

/* ניווט הכפתורים */
.date-picker-container .react-datepicker__navigation {
  top: 15px !important;
  width: 50px !important;
  height: 50px !important;
  border: none !important;
  background-color: transparent !important;
  color: #bb86fc !important;
}

.date-picker-container .react-datepicker__navigation--previous::before,
.date-picker-container .react-datepicker__navigation--next::before {
  border-color: #bb86fc !important;
  border-width: 0 7px 7px 0 !important;
  width: 25px !important;
  height: 25px !important;
  padding: 8px !important;
}
